










export default {
	props:{
		position:{
			type:String,
			default:null 
		},
		imgCount:{
			type: Number,
			default: 0
		},
		imgMaxCount:{
			type: Number,
			default: 0
		},
		videoCount:{
			type: Number,
			default: 0
		},
		videoMaxCount:{
			type: Number,
			default: 0
		}
	},
	data() { 
		return { 
			popshow:false,
			hideanimation:false,
		};
	},
	methods:{
		// Toshow popup page
		show:function(){
			this.popshow = true;
		},
		// Tohide popup page
		close:function(){
			let that = this;
			that.hideanimation = true;
			if(that.position==null){
				that.popshow = false;
			}else{
				setTimeout(function(){
					that.popshow = false;
					that.hideanimation = false;
				},500)
			}
		}
	},
}
