
















































import lvvPopup from './lvv-popup/lvv-popup.vue'
export default {
	props:{
	},
	components:{
		lvvPopup
	},
	data() {
		return {
			videoMaxCount:1,
			maxCount: 9,
			count: 9,
			videoSrc:'',
			imgArray:[],
			imgSrc:''
		}
	},
	methods: {
		// 显示modal弹出框
		toshow(){
			this.$refs.lvvpopref.show();
		},
		// 关闭modal弹出框
		toclose(){
			this.$refs.lvvpopref.close();
		},
		chooseImg:function(e){
			var type = e.currentTarget.dataset.type;
			var _that =this;
			var count = _that.count;
			uni.chooseImage({
				count: count,
				sourceType:[type],
				success:function(res){
					for(let i in res.tempFilePaths){
						_that.imgArray.push(res.tempFilePaths[i]);
					}
					_that.count = count - res.tempFilePaths.length;
					_that.toclose();
				}
			})
		},
		closeImg:function(e){
			let index = e.currentTarget.dataset.index;
			this.imgArray.splice(index,1);
			this.count = this.count +1;
		},
		chooseVideo:function(e){
			var type = e.currentTarget.dataset.type;
			var _that =this;
			uni.chooseVideo({
				count:_that.videoMaxCount,
				sourceType:[type],
				success: function (res) {
					_that.videoSrc = res.tempFilePath;
					_that.toclose();
				}
			})
		},
		clearVideo:function(e){
			this.videoSrc = ""
		}
	}
}
